body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.is-visible {
  display: block !important;
}

.input-group-field.amount-input {
  color: transparent;
  text-shadow: 0 0 0 #000;
}

.budget-input-group {
  margin: 0;
}

.category-budget th {
  width: 33.33%;
}


.main-container {
  margin-top: 2em;
}

.delete-expense.button {
  margin: 0;
}

.form-error-container {
  margin-top: -.5rem;
  height: 1.75em;
}

.input-group-field.amount-input {
  text-align: right;
}

.input-group-field.amount-input, .expense-form-category {
  height: 2em;
  font-size: 2em;
}

.input-group-field.budget-input {
  height: 2em;
  font-size: 1.5em;
}

.dropdown.menu .menu-text a {
  color: #0a0a0a;
  padding: 0;
}

.input-group input[name='amount'] {
  margin: 0;
}



